	module.exports = {"data":{"wordpressPost":{"title":"Winter is coming &#8230; Make sure your skin is ready","content":"<p>Winter brings with it a raft of super cool stuff. Open fires, furry scarves and an excellent excuse to eat as much pasta as you fancy. One thing it doesn’t do, however, is care for your skin. Still, with a little effort you can keep skin in perfect nick until the warmer weather rolls around. Here’s how, says Ultraceuticals skin expert Tracey Beeby</p>\n<p><strong>Winter is here &#8230;</strong></p>\n<p>That means skin is more likely to suffer from dryness and dehydration (lipid and moisture loss) due to cold winds, artificial heating, heavier comfort foods, less exercise and long, hot showers.</p>\n<p><strong>You can prepare for that by &#8230;</strong></p>\n<p>Upgrading your skin care routine with a good moisturiser and a hydrating serum to keep your skin super hydrated. Additionally, a nourishing or replenishing mask will help replace moisture and repair our skin’s epidermis and skin barrier.</p>\n<p><strong>Winter skin means always &#8230;</strong></p>\n<p>Keep hydrated by drinking plenty of water throughout the day and avoiding those tempting long hot showers. Use the colder season to your advantage as it’s also a great time for treatments such as peels when the sun is not as strong and most of your activity is indoors.</p>\n<p><strong>And never&#8230;</strong></p>\n<p>Miss wearing you daily broad spectrum sunscreen. Although the UVB may be less likely to cause burning, UVA is present all year round which can still be damaging to our skin through photo-aging, so your daily protection should never be missed.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2016/08/31140103/Ultraceuticals_30.3.16-3.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"ca9a152a-b24e-5216-816d-237b72be1551"}}

